html {
  font-size: 20px;
  font-weight: 400;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

textarea {
  margin: 0.3rem;
}
.preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: fit-content;
}
.printPreview {
  font-size: 0.8rem;
  display: flex;
  width: fit-content;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.topSection {
  padding: 2rem 0 0;
  display: block;
}
.part1Left {
  float: left;
  margin-right: 2rem;
}
.part1Right {
  float: right;

  margin-left: 2rem;
}
.part1Left > p,
.part1Right > p {
  margin: 0.2rem;
}

.about {
  width: 30rem;
  border: 1px solid black;
  display: flex;
}
.about > p {
  font-size: 0.8rem;
  width: 30rem;
  flex-wrap: wrap;
  margin: 0.3rem;
}

.toggle > * {
  margin: 0.3rem;
}
.toggle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.app {
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  border: 0.1rem solid rgb(243, 200, 135);
  width: 32rem;
  border-radius: 1rem;
  height: fit-content;
  align-items: center;
  padding: 0.1rem;
  justify-content: center;
}

h1 {
  text-align: center;
  text-decoration: underline;
}
label {
  display: flex;
  flex-direction: column;
}

input {
  border-radius: 0.3rem;
  height: 1.6rem;
  border: 0.1rem solid rgb(243, 200, 135);
  border-radius: 0.3rem;
  font-size: 0.8rem;
  color: rgb(98, 119, 119);
}

textarea {
  border-radius: 0.3rem;
  border: 0.1rem solid rgb(243, 200, 135);
  border-radius: 0.3rem;
  font-size: 0.8rem;
  color: rgb(98, 119, 119);
}

input[type="text"] {
  width: 25rem;
  margin: 0.3rem;
}
input[type="email"],
input[type="tel"] {
  width: 14rem;

  margin: 0.3rem;
}

input[type="date"] {
  width: 10rem;

  margin: 0.3rem;
}
.edu1,
.edu2,
.job1,
.job2 {
  margin: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid rgb(243, 200, 135);
}
form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
button {
  justify-self: center;
  align-self: center;
  margin: 1rem;
  padding: 0.5rem;
  background-color: rgb(238, 238, 195);
  border-radius: 0.3rem;
  border: 1px solid rgb(243, 200, 135);
}

.section1,
.section4,
.section5,
.section6 {
  margin: 1rem;
  padding: 1rem;

  border-radius: 0.5rem;
  border: 1px solid rgb(243, 200, 135);
}
img {
  width: 7rem;
  height: 7rem;
}

.skillsSpec {
  width: 25rem;
  word-wrap: break-word;
  margin: 0.3rem;
}
li {
  margin: 0.3rem;
  width: 26rem;
  word-wrap: break-word;
}
table,
th,
tr,
td {
  padding: 0.5rem;
  border-collapse: collapse;
  border: 0.1rem solid black;
  margin: 1.2rem;
}
